<script>
  import { onMount } from "svelte";
  import { fade } from "svelte/transition";
  import { settingUI } from "../store/setting.js";
  import {
    deviceList,
    isSerialConnect,
    serialConnect,
    serialWrite,
    serialportList,
  } from "../store/serial.js";

  let serialStart = false;
  $: if ($isSerialConnect === false) {
    serialStart = false;
  }
  const setSerialConnect = async (path) => {
    await serialConnect(path);
    setTimeout(() => {
      serialWrite("0");
      setTimeout(() => {
        serialWrite("1");
        setTimeout(() => {
          serialStart = true;
        }, 5000);
      }, 8000);
    }, 3000);
    // isSerialConnect.set(true);
    // serialStart = true;
  };
  //창크기 설정 renderer.js에서 setWindowSize 호출
  window.setWindowSize(
    $settingUI.defaultWindow.x,
    $settingUI.defaultWindow.y,
    $settingUI.defaultWindow.width,
    "main"
  );

  onMount(() => {
    serialportList();
  });
</script>

{#if $isSerialConnect === false || serialStart === false}
  <div class="modal" transition:fade={{ duration: 200 }}>
    <div class="area">
      {#if $isSerialConnect === false}
        <div class="disconnect">
          <h2>연결되지 않았습니다.</h2>
          {#each $deviceList as device}
            <button
              type="button"
              on:click={() => {
                setSerialConnect(device.path);
              }}>{device.path} 연결하기</button
            >
          {/each}
        </div>
      {:else}
        <div class="start">리얼필드 동작을 시작합니다</div>
      {/if}
    </div>
  </div>
{/if}

<style>
  .modal {
    z-index: 400;
  }
  .modal .area {
    background: none;
  }

  div.start {
    background-color: #fff;
    color: #000;
    padding: 70px;
    font-size: 30px;
    border-radius: 7px;
  }
  .disconnect {
    background-color: #fff;
    padding: 70px;
    font-size: 30px;
    border-radius: 7px;
    width: 800px;
  }
  .disconnect h2 {
    color: #cc4c4c;
    font-size: 30px;
    font-weight: 700;
    text-align: center;
  }
  .disconnect button {
    background-color: #00d1b2;
    color: #fff;
    padding: 10px;
    font-size: 16px;
    width: 100%;
    border-radius: 7px;
    text-align: left;
  }
  .disconnect button:hover {
    background-color: #009e86;
  }
</style>
