<script>
  import { showHelp, isAutomode } from "../store/ui.js";
  import { settingUI } from "../store/setting.js";
  import { nowFreesetCategory } from "../store/freeset.js";
</script>

<footer class="footer">
  <div class="inner">
    <h2>유의사항</h2>
    <ul>
      <li>8~10초 동안 기계가 동작합니다.</li>
      <li>
        안전을 위해 <span class="red"
          >반드시 조작이 모두 끝난 후에 올라가시기</span
        > 바랍니다.
      </li>
    </ul>
    <div class="fnb">
      {#if $settingUI.viewAutomode}
        <button
          type="button"
          class="automode"
          on:click={() => {
            isAutomode.set(true);
          }}>자동모드</button
        >
      {/if}
      {#if $settingUI.viewFavorite}
        <button
          type="button"
          class="favorite"
          class:on={$nowFreesetCategory === 99}
          on:click={() => {
            if ($nowFreesetCategory === 99) {
              nowFreesetCategory.set(0);
            } else {
              nowFreesetCategory.set(99);
            }
          }}>Favorite</button
        >
      {/if}
      <button
        type="button"
        class="help"
        on:click={() => {
          showHelp.set(true);
        }}>Help</button
      >
    </div>
  </div>
</footer>

