<script>
  import { fade } from "svelte/transition";
  import { showAdmin, viewLoading } from "../store/ui.js";
  import { freeset, nowFreeset } from "../store/freeset.js";
  import { settingUI } from "../store/setting.js";
  import { serialWrite } from "../store/serial.js";
  let isQuit = false;
  export const appQuit = () => {
    isQuit = true;
    serialWrite("0");
    setTimeout(() => {
      serialWrite("Q");
      setTimeout(() => {
        window.close();
      }, 5000);
    }, 6000);
  };
</script>

{#if $showAdmin}
  <div class="modal" transition:fade={{ duration: 200 }}>
    <div class="area">
      {#if isQuit === true}
        <div class="quit">리얼필드를 종료합니다</div>
      {:else}
        <h1>관리자 모드</h1>
        <p class="warning">※ 관리자를 위해 만들어진 버튼입니다.</p>
        {#if $settingUI.viewReset === true}
          <div class="buttonArea">
            <button
              type="button"
              class="confirm"
              on:click={() => {
                const el = $freeset.filter((el) => el.id === 91)[0];
                serialWrite(el.middle);
                nowFreeset.set(91);
                showAdmin.set(false);
                viewLoading(91);
              }}>리셋(하)</button
            >
            <button
              type="button"
              class="confirm"
              on:click={() => {
                const el = $freeset.filter((el) => el.id === 92)[0];
                serialWrite(el.middle);
                nowFreeset.set(92);
                showAdmin.set(false);
                viewLoading(92);
              }}>리셋(상)</button
            >
          </div>
        {/if}
        <div class="buttonArea">
          <button
            type="button"
            on:click={() => {
              showAdmin.set(false);
            }}>취소</button
          >
          <button
            type="button"
            class="quit"
            on:click={() => {
              appQuit();
            }}>종료</button
          >
        </div>
      {/if}
    </div>
  </div>
{/if}

