  .modal .area {
    width: 417px;
    height: fit-content;
    text-align: center;
    padding: 32px 0;
  }
  .modal .area h1 {
    font-size: 19px;
    font-weight: 700;
    line-height: 22px;
  }
  .modal .area p {
    margin-top: 22px;
    font-size: 16px;
    line-height: 22px;
  }
  .modal .area p.warning {
    color: #cc4c4c;
  }
  .modal .area div.buttonArea {
    margin-top: 22px;
  }
  .modal .area div.buttonArea button {
    margin: 0 5px;
    width: 136px;
    height: 40px;
    background: #e4e4e4;
    border-radius: 6px;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #9d9d9d;
  }
  .modal .area div.buttonArea button.confirm {
    background: #20cfa5;
    color: #fff;
  }
  .modal .area div.buttonArea button.quit {
    background: #cc4c4c;
    color: #fff;
  }

  .modal .area div.quit {
    background-color: #fff;
    color: #000;
    padding: 0 50px;
    font-size: 16px;
    border-radius: 7px;
  }
</style>
