import { writable, get } from 'svelte/store';
import { selectedField, nowFreeset, freeset } from './freeset.js';
import { selectedHeight, serialWrite } from './serial.js';
import { settingUI } from './setting';

/* 필드 레이아웃 small, big */
export const nowLayout = writable('small');

/* 현재 윈도우 상태 main, small */
export const nowWindowStatus = writable('main');


/* popup */
export const showAdmin = writable(false);
export const showField = writable(false);
export const showHelp = writable(false);
export const showReset = writable(false);
export const showLoading = writable(false);
export const showSetting = writable(false);

//로딩화면 보기
export let viewLoadingInterval;
export const loadingCount = writable(8);
export const viewLoading = (id) => {
  selectedField.set(id);
  showLoading.set(true);
  if(get(selectedHeight) === 'high') {
    loadingCount.set(8);
  } else {
    loadingCount.set(5);
  }
  viewLoadingInterval = setInterval(() => {
    loadingCount.update(val=> val-0.04);
    if (get(loadingCount) < 0) {
      clearInterval(viewLoadingInterval);
      nowFreeset.set(get(selectedField));
      showLoading.set(false);
      if(get(settingUI).smallWindow.auto === true) {
        nowWindowStatus.set("small");
        window.setWindowSize(
          get(settingUI).smallWindow.x,
          get(settingUI).smallWindow.y,
          get(settingUI).smallWindow.width,
          "small"
        );
      }
    }
  }, 40);
};


/* 자동모드 */
export const isAutomode = writable(false);
let oldAutomode = true;
let automodeInterval;

const sendRandomMessage = () => {
  const tmp = get(freeset).filter((el)=> el.listing === true);
  const random = Math.floor(Math.random() * tmp.length);
  const sendMessage = tmp[random].high;
  serialWrite(sendMessage);

}
isAutomode.subscribe((nowMode) => {
  if(nowMode === true && oldAutomode === false){
    //자동모드 시작
    oldAutomode = true;
    sendRandomMessage();
    automodeInterval = setInterval(() => {
      sendRandomMessage();
    }, 15 * 1000);
  }
  if(nowMode === false && oldAutomode === true){
    clearInterval(automodeInterval);
    //자동모드 종료
    oldAutomode = false;
  }

});