<script>
  import { nowLayout, nowWindowStatus } from "../store/ui.js";
  import { settingUI } from "../store/setting.js";
  import { nowFreesetCategory, freesetCategory } from "../store/freeset.js";
</script>

<header class="header">
  <img
    src="asset/images/ui/logo.svg"
    alt="logo"
    class="logo"
    class:left={$settingUI.viewLogo === "left"}
  />
  <button
    class="window-minimize"
    on:click={() => {
      nowWindowStatus.set("small");
      window.setWindowSize(
        $settingUI.smallWindow.x,
        $settingUI.smallWindow.y,
        $settingUI.smallWindow.width,
        "small"
      );
    }}>Window Minmize</button
  >
  {#if $nowFreesetCategory !== 99}
    <ul class="nav">
      {#each $freesetCategory.filter((el) => el.id < 99) as { id, name }}
        <!-- svelte-ignore a11y-click-events-have-key-events -->
        <li
          on:click={() => {
            nowFreesetCategory.set(id);
          }}
          class:on={$nowFreesetCategory === id}
        >
          {name}
        </li>
      {/each}
    </ul>
  {:else}
    <button
      type="button"
      class="closeFavorite"
      on:click={() => {
        nowFreesetCategory.set(0);
      }}>Close Favorite</button
    >
  {/if}
  <div class="layout">
    <button
      class="layout-small"
      on:click={() => {
        nowLayout.set("small");
      }}
      class:on={$nowLayout === "small"}>Small Layout</button
    >
    <button
      class="layout-big"
      on:click={() => {
        nowLayout.set("big");
      }}
      class:on={$nowLayout === "big"}>Big Layout</button
    >
  </div>
  <!--button class="small"
    ><img src="asset/images/ui/btnSmall.svg" alt="window small" /></button
  -->
</header>

