<script>
  import { fade } from "svelte/transition";
  import { showField, viewLoading } from "../store/ui.js";
  import { settingUI } from "../store/setting.js";
  import { serialWrite, selectedHeight } from "../store/serial.js";
  import {
    nowFreeset,
    freesetFavorite,
    freeset,
    selectedField,
    selectedFieldInfo,
  } from "../store/freeset.js";
  import ToastFavorite from "./toastFavorite.svelte";
  let toastFavoriteComponent;

  //필드 팝업 보기
  export const viewFieldPopup = (id) => {
    selectedField.set(id);
    showField.set(true);
  };

  //높이 설정
  function onHeightChange(event) {
    selectedHeight.set(event.currentTarget.value);
  }
</script>

{#if $showField}
  <div class="modal" transition:fade={{ duration: 200 }}>
    <div class="area">
      <h1>
        {$selectedFieldInfo.name.replace("<br>", " ")}
        <button
          type="button"
          class="close"
          on:click={() => {
            showField.set(false);
          }}>close</button
        >
      </h1>
      <div class="basicArea">
        <svg viewBox="0 0 455 272" class="img">
          <image
            href="asset/images/freeset/field{$selectedFieldInfo.id}.png"
            x="0"
            y="0"
          />
        </svg>
        <hr class="bar" />
        <div class="actionArea">
          <h2>강도설정</h2>
          <p>{$selectedFieldInfo.desc}</p>
          {#if $settingUI.viewTop}
            <input
              type="radio"
              name="strength"
              id="strength-top"
              value="high"
              checked={$selectedHeight === `high` &&
                $selectedFieldInfo.high !== ""}
              on:change={onHeightChange}
              disabled={$selectedFieldInfo.high === ""}
            />
            <label for="strength-top"> 상 (경사 가파르게)</label>
          {/if}
          <input
            type="radio"
            name="strength"
            id="strength-middle"
            value="middle"
            checked={$selectedHeight === `middle` ||
              ($selectedFieldInfo.high === "" && $selectedFieldInfo.low === "")}
            on:change={onHeightChange}
          />
          <label for="strength-middle"> 중 (경사 보통)</label>
          <input
            type="radio"
            name="strength"
            id="strength-low"
            value="low"
            checked={$selectedHeight === `low` && $selectedFieldInfo.low !== ""}
            on:change={onHeightChange}
            disabled={$selectedFieldInfo.low === ""}
          />
          <label for="strength-low"> 하 (경사 완만하게)</label>
          <button
            type="button"
            class="favorite"
            class:on={$freesetFavorite.includes($selectedFieldInfo.id) === true}
            on:click={() => {
              toastFavoriteComponent.setFavorite($selectedFieldInfo.id);
            }}>favorite</button
          >
          <button
            type="button"
            class="load"
            on:click={() => {
              let setHeight;
              if (
                $selectedFieldInfo.high === "" ||
                $selectedFieldInfo.low === ""
              ) {
                selectedHeight.set("middle");
              }
              if ($selectedHeight === "high") {
                setHeight = $selectedFieldInfo.high;
              } else if ($selectedHeight === "low") {
                setHeight = $selectedFieldInfo.low;
              } else {
                setHeight = $selectedFieldInfo.middle;
              }
              showField.set(false);
              serialWrite(setHeight);
              viewLoading($selectedFieldInfo.id);
            }}
          >
            <img src="asset/images/ui/iconPlay.svg" alt="" />
            지형 불러오기
          </button>
        </div>
      </div>
      <div class="voteArea">
        <h2>{$selectedFieldInfo.voteName} 지형으로 한 다른 지형</h2>
        <ul>
          {#each $freeset
            .filter((el) => el.category === $selectedFieldInfo.category)
            .filter((el) => el.id !== $selectedFieldInfo.id)
            .filter((el) => el.listing === true) as item}
            <li
              class:on={$nowFreeset === item.id}
              on:click={() => {
                viewFieldPopup(item.id);
              }}
            >
              <svg viewBox="0 0 455 272" class="img">
                <image
                  href="asset/images/freeset/field{item.id}.png"
                  x="0"
                  y="0"
                />
                <image
                  href="asset/images/freeset/foot{item.id}.png"
                  x="115"
                  y="48"
                  transform="scale(0.93)"
                />
              </svg>
              <div class="desc">
                <h3>{@html item.name}</h3>
              </div>
            </li>
          {/each}
        </ul>
      </div>
    </div>
  </div>
{/if}
<ToastFavorite bind:this={toastFavoriteComponent} />

