import { get, writable } from 'svelte/store';
// eslint-disable-next-line no-undef
const { SerialPort } = require("serialport");

export const isSerialConnect = writable(false); //연결여부
export const selectedHeight = writable('middle'); //상중하
export const deviceList = writable([]); //연결된 기기 목록
export let selectedSerialPort = writable(); //선택된 시리얼포트

const baudRate = 115200; //

//연결
export const serialConnect = async (path) => {
  const port = new SerialPort({
    path: path,
    baudRate: baudRate,
  }, function (err) {
    if (err) {
      alert('연결실패 : 연결을 확인하고 다시 시도해주세요.');
      isSerialConnect.set(false);
  
      return console.log('Error: ', err.message)
    }
  });
  console.log(port);

  port.on('close', function() {
    isSerialConnect.set(false);
    console.log('serial disconnect');
  });  

  port.on('readable', function () {
    console.log('Data1:', port.read())
  });

  port.on('data', function (data) {
    console.log('Data2:', data)
  });

  selectedSerialPort.set(port);
  isSerialConnect.set(true);

};

//포트 목록
export const serialportList = async () => {
  try {
    await SerialPort.list().then((ports, err) => {
      if (err) {
        return err;
      } else {
        deviceList.set(ports);
      }
    });
  } catch (err) {
    return err;
  }
}

export const serialWrite = (val) => {
  const data = new Uint8Array([val.charCodeAt(0)]);
  const port = get(selectedSerialPort);
    port.write(data, function(err){
      if(err){
        isSerialConnect.set(false);
        console.log('serialWrite', err);
      }
      console.log(`send ${val}`);
    });
};

