import { writable } from 'svelte/store'
/*
기기 버전 리스트 및 현재 기기버전
*/
export const deviceList = [
  {name:'dr05', type:'v0'},
  {name:'dr11', type:'v1'}
];
const storedNowDevice = localStorage.nowDevice;
export const nowDevice = writable(storedNowDevice || deviceList[0].name);
nowDevice.subscribe((value) => localStorage.nowDevice = value);

/*
UI 설정
*/
const storedSettingUI = localStorage.settingUI && JSON.parse(localStorage.settingUI);
export const settingUI = writable(storedSettingUI || {
	viewLogo: 'center', //로고위치 left, center
	viewTop: false, //상모드 노출여부
	viewAutomode: false, //자동모드 노출여부
	viewFavorite: false, //관심모드 노출여부
  viewReset: false, //리셋버튼 노출여부
	defaultWindow: {
    x: 'center', //left, center, right, left부터의 숫자
    y: 'center', //top, center, bottom, top으로부터의 숫자
    width: 1200
  },
	smallWindow: {
    x: 'center', //left, center, right, left부터의 숫자
    y: 'bottom', //top, center, bottom, top으로부터의 숫자
    width: 200,
    auto: true //자동 최소화
  }
});
settingUI.subscribe((value) => localStorage.settingUI = JSON.stringify(value));