<script>
  import { nowLayout } from "../store/ui.js";
  import { settingUI } from "../store/setting.js";
  import {
    nowFreeset,
    nowFreesetCategory,
    freeset,
    freesetFavorite,
  } from "../store/freeset.js";
  import ToastFavorite from "./toastFavorite.svelte";
  let toastFavoriteComponent;
  import PopupField from "./popupField.svelte";
  let popupFieldComponent;

  //카테고리 반영
  let freesetList;
  $: if ($nowFreesetCategory === 99) {
    //favorite mode
    freesetList = $freeset
      .filter((el) => el.listing === true)
      .filter((el) => $freesetFavorite.includes(el.id));
  } else if ($nowFreesetCategory > 0) {
    //카테고리
    freesetList = $freeset
      .filter((el) => el.listing === true)
      .filter((el) => el.category === $nowFreesetCategory);
  } else {
    //전체보기
    freesetList = $freeset.filter((el) => el.listing === true);
  }
</script>

<main class="main">
  <ul class:big={$nowLayout == "big"}>
    {#each freesetList as item}
      <li class:on={$nowFreeset === item.id}>
        <svg
          viewBox="0 0 455 272"
          class="img"
          on:click={() => {
            popupFieldComponent.viewFieldPopup(item.id);
          }}
        >
          <image href="asset/images/freeset/field{item.id}.png" x="0" y="0" />
        </svg>
        <div class="desc">
          <h3
            on:click={() => {
              popupFieldComponent.viewFieldPopup(item.id);
            }}
          >
            {@html item.name}
          </h3>
          {#if $settingUI.viewFavorite}
            <button
              type="button"
              class="favorite"
              class:on={$freesetFavorite.includes(item.id) === true}
              on:click={() => {
                toastFavoriteComponent.setFavorite(item.id);
              }}>favorite</button
            >
          {/if}
        </div>
      </li>
    {/each}
  </ul>
</main>
<ToastFavorite bind:this={toastFavoriteComponent} />
<PopupField bind:this={popupFieldComponent} />

