<script>
  import { fade } from "svelte/transition";
  import { showReset, viewLoading } from "../store/ui.js";
  import { freeset, nowFreeset } from "../store/freeset.js";
  import { serialWrite } from "../store/serial.js";
</script>

{#if $showReset}
  <div class="modal" transition:fade={{ duration: 200 }}>
    <div class="area">
      <h1>“리얼필드” RESET (초기화)<br />모드를 진행하시겠습니까?</h1>
      <p>
        확인 시 리얼필드 초기설정 모드<br />(평지 모드)로 제품이 원위치로
        복귀됩니다.
      </p>
      <p class="warning">※ 조작이 모두 끝난 후에 올라가주세요.</p>
      <div class="buttonArea">
        <button
          type="button"
          on:click={() => {
            showReset.set(false);
          }}>취소</button
        >
        <button
          type="button"
          class="confirm"
          on:click={() => {
            const el = $freeset.filter((el) => el.id === 91)[0];
            serialWrite(el.middle);
            nowFreeset.set(91);
            showReset.set(false);
            viewLoading(91);
          }}>확인</button
        >
      </div>
    </div>
  </div>
{/if}

