<script>
  import { freesetFavorite } from "../store/freeset.js";

  /* toast */
  let toastFavorite = null;
  let toastExit;
  export const setFavorite = (id) => {
    if ($freesetFavorite.includes(id)) {
      $freesetFavorite = $freesetFavorite.filter((el) => el !== id);
      toastFavorite = "remove";
      clearTimeout(toastExit);
      toastExit = setTimeout(() => {
        toastFavorite = null;
      }, 2000);
    } else {
      $freesetFavorite.push(id);
      toastFavorite = "add";
      clearTimeout(toastExit);
      toastExit = setTimeout(() => {
        toastFavorite = null;
      }, 2000);
    }
    $freesetFavorite = $freesetFavorite;
  };
</script>

{#if toastFavorite !== null}
  <div class="toastFavoriteAdd">
    {#if toastFavorite === "add"}
      <img src="asset/images/ui/iconToastFavoriteAdd.svg" alt="" />
      관심 목록에 추가되었습니다.
    {:else}
      <img src="asset/images/ui/iconToastFavoriteRemove.svg" alt="" />
      관심 목록에서 삭제되었습니다.
    {/if}
  </div>
{/if}

<style>
  .toastFavoriteAdd {
    position: fixed;
    z-index: 300;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 364px;
    height: 50px;
    background-color: #eed3d3;
    border: 2px solid #e2b6b6;
    font-size: 18px;
    border-radius: 7px;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
    padding: 8px 24px;
    transition: 0.2s;
  }
  .toastFavoriteAdd img {
    display: inline-block;
    width: 24px;
    height: 24px;
    vertical-align: middle;
    margin-right: 10px;
  }
</style>
