<script>
  import { fade } from "svelte/transition";
  import { showSetting } from "../store/ui.js";
  import { settingUI } from "../store/setting.js";
  const settingPassword = "0790";
  let nowPassword = "";
  let nowPasswordLength = "";
  $: nowPasswordLength = "*".repeat(nowPassword.length);
  const inputPassword = (val) => {
    if (val === "reset") {
      nowPassword = "";
    } else if (val === "back" && nowPassword.length > 0) {
      nowPassword = nowPassword.slice(0, -1);
    } else if (nowPassword.length < 9) {
      nowPassword += val;
    }
  };
  //창크기 설정 renderer.js에서 setWindowSize 호출
  const setWindow = () => {
    window.setWindowSize(
      $settingUI.defaultWindow.x,
      $settingUI.defaultWindow.y,
      $settingUI.defaultWindow.width,
      "main"
    );
  };
</script>

{#if $showSetting}
  <div class="modal" transition:fade={{ duration: 200 }}>
    <div class="area">
      {#if nowPassword === settingPassword}
        <h1>Setting</h1>
        <dl class="inputArea">
          <dt>로고위치</dt>
          <dd>
            <label>
              <input
                type="radio"
                value="left"
                bind:group={$settingUI.viewLogo}
              />
              좌측
            </label>
            <label>
              <input
                type="radio"
                value="center"
                bind:group={$settingUI.viewLogo}
              />
              가운데
            </label>
          </dd>
          <dt>상모드</dt>
          <dd>
            <label>
              <input type="checkbox" bind:checked={$settingUI.viewTop} />
              노출
            </label>
          </dd>
          <dt>자동모드</dt>
          <dd>
            <label>
              <input type="checkbox" bind:checked={$settingUI.viewAutomode} />
              노출
            </label>
          </dd>
          <dt>관심모드</dt>
          <dd>
            <label>
              <input type="checkbox" bind:checked={$settingUI.viewFavorite} />
              노출
            </label>
          </dd>
          <dt>리셋버튼</dt>
          <dd>
            <label>
              <input type="checkbox" bind:checked={$settingUI.viewReset} />
              노출
            </label>
          </dd>
          <dt>기본 창</dt>
          <dd>
            X <input type="text" bind:value={$settingUI.defaultWindow.x} />, Y
            <input type="text" bind:value={$settingUI.defaultWindow.y} /><br />
            가로
            <input type="text" bind:value={$settingUI.defaultWindow.width} />
          </dd>
          <dt>최소화 창</dt>
          <dd>
            X <input type="text" bind:value={$settingUI.smallWindow.x} />, Y
            <input type="text" bind:value={$settingUI.smallWindow.y} /><br />
            가로
            <input type="text" bind:value={$settingUI.smallWindow.width} /><br
            />
            <label>
              <input
                type="checkbox"
                bind:checked={$settingUI.smallWindow.auto}
              />
              동작 후 자동 최소화
            </label>
          </dd>
        </dl>
      {:else}
        <h1>Input Password</h1>
        <div class="password-length">{nowPasswordLength}</div>
        <div class="keypad">
          {#each ["1", "2", "3", "4", "5", "6", "7", "8", "9", "reset", "0", "back"] as item}
            <button
              on:click={() => {
                inputPassword(item);
              }}>{item}</button
            >
          {/each}
        </div>
      {/if}
      <div class="buttonArea">
        <button
          type="button"
          on:click={() => {
            nowPassword = "";
            showSetting.set(false);
          }}>닫기</button
        >
        {#if nowPassword === settingPassword}
          <button
            type="button"
            class="confirm"
            on:click={() => {
              setWindow();
            }}>창크기 적용</button
          >
        {/if}
      </div>
    </div>
  </div>
{/if}

