  .modal .area {
    width: 417px;
    height: fit-content;
    text-align: center;
    padding: 32px 0;
  }
  .modal .area h1 {
    font-size: 19px;
    font-weight: 700;
    line-height: 22px;
  }

  .modal .area div.buttonArea {
    margin-top: 22px;
  }
  .modal .area div.buttonArea button {
    margin: 0 5px;
    width: 136px;
    height: 40px;
    background: #e4e4e4;
    border-radius: 6px;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #9d9d9d;
  }
  .modal .area div.buttonArea button.confirm {
    background: #20cfa5;
    color: #fff;
  }

  .modal .area .password-length {
    margin: 10px auto;
    padding: 0 10px;
    width: 160px;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
  }

  .modal .area .keypad {
    margin: 0 auto;
    width: 160px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .modal .area .keypad button {
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 16px;
    font-weight: 700;
    color: #fff;
    background-color: #666;
    margin: 1px;
  }

  .modal .area .inputArea {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 410px;
    padding: 10px;
  }

  .modal .area .inputArea dt {
    margin: 1px;
    width: 100px;
    font-weight: 700;
    font-size: 14px;
    padding: 5px;
    text-align: left;
    line-height: 200%;
    background-color: #fafafa;
  }

  .modal .area .inputArea dd {
    margin: 1px;
    width: 230px;
    font-size: 14px;
    text-align: left;
    padding: 5px;
    line-height: 200%;
  }

  .modal .area .inputArea input[type="text"] {
    width: 60px;
    line-height: 140%;
  }
  .modal .area .inputArea label:not(:last-child) {
    margin-right: 10px;
  }
</style>
